html{
  font-size: 65% !important;
}
body{
  font-size: 1.6rem;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
}
a{
  display: inline-block;
  outline: none;
  text-decoration: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Back To Top Button */
.BackToTop{
  position: fixed;
  bottom: 7rem;
  right: 5rem;
  z-index: 200;
  transition: all linear .3s;
}
.BackToBtn{
  color: rgb(255, 252, 252);
  background: #000;
  font-size: 4rem;
  padding: .5rem;
  display: none;
  opacity: 0;
  -webkit-box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.234); 
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.2);
}
.BackToBtn:hover{
  color: red;
}

.BackToBtnActive{
  color: rgb(255, 252, 252);
  background: #000;
  font-size: 4rem;
  padding: .5rem;
  display: inline-block !important;
  opacity: 1;
  transition: all linear .2s;
  -webkit-box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.234); 
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.2);
}