
/* Font family
font-family: 'Karla', sans-serif;
font-family: 'Raleway', sans-serif;
*/

/* ==================================
  Variabble
=================================== */
$fon-b:#000;
$clr-o:#e72828;
$fon-w:#fcfcfc;
$font-fp:'Raleway', sans-serif;
$font-fs:'Karla', sans-serif;
/* ==================================
  EXTEND
=================================== */
%sec{
  .SectionHeading{
    p{
      font-family: $font-fs;
      font-size: 1.4rem;
      font-weight: 600;
      margin-bottom: 0 ;
      color: $clr-o;
    }
    h2{
      font-family: $font-fp;
      font-size: 3.6rem;
      font-weight: 700;
      margin-bottom: 2rem;
    }
  }
}
%BTC{
  .BtnBTC{
    margin-top: 2rem;
    text-decoration: none;
    outline: none;
    display: inline-block;
    padding: .5rem 1.5rem;
    background: #000;
    color: #fcfcfc;
    transition: all linear .3s;
    &:hover{
      background: $clr-o;
    }
  }
}
%CTB{
  .BtnCTB{
    text-decoration: none;
    outline: none;
    display: inline-block;
    padding: .5rem 1.5rem;
    background: $clr-o;
    color: #fcfcfc;
    transition: all linear .3s;
    &:hover{
      background: #000;
    }
  }
}
%BGB{
  background: url('../images/video/VideoBG.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
}
/* ==================================
  TopHeader Componenet Start
=================================== */
p{
  margin-bottom: 0;
  font-family: $font-fp;
  font-size: 1.6rem;
  span{
    color: $clr-o;
    font-size: 1rem;
  }
}
h3{
  font-family:$font-fs;
  color: $fon-w;
  font-size: 1.4rem;
  margin-bottom: 0;
  text-align: end;
}
.hederTopLinks{
  text-align: end;
  a{
    color: $fon-w;
    font-size: 1;
    margin-left: .5rem;
    transition: all ease .3s;
    &:hover{
      color: $clr-o;
    }
  }
}
/* ==================================
  TopHeader Componenet End
=================================== */
/* ==================================
  Main Menu Componenet Start
=================================== */
.mainMenu,
.mainMenuFixed{
  padding: 1rem 0;
  transition: all linear .2s;
  .mainBrand{
    font-size: 2rem;
    color:$fon-b;
    font-weight: 600;
    font-family: $font-fs;
    span{
      color:$clr-o ;
      margin-right: 1rem;
      font-size: 3rem;
    }
  }
  .mainUl{
    a{
      color: $fon-b !important;
      font-family: $font-fp;
      font-weight: 600;
      display: block;
      margin: 0 1rem;
      text-decoration: none;
      position: relative;
      z-index: 2;
      &::before ,
      .active{
        position: absolute;
        content: '';
        width: 0;
        height: 2px;
        left: 50%;
        transform: translateX(-50%);
        top: 0;
        background: $clr-o;
        transition: all linear .2s;
        z-index: -1;
      }
      &:hover::before{
        width: 100%
      }
    }
  }
  @extend %CTB;
}
.mainMenuFixed{
  position: sticky;
  width: 100%;
  left: 0;
  top: 0;
  background: rgb(255, 255, 255) !important;
  -webkit-box-shadow: 0px 6px 9px 2px rgba(167, 167, 167, 0.36);
  box-shadow: 0px 6px 9px 2px rgba(167, 167, 167, 0.36);
  z-index: 100;
  padding: 1.5rem;
}
/* ==================================
  Main Menu Componenet End
=================================== */
/* ==================================
  Page Top Componenet Start
=================================== */
.PageTopSection{
  padding: 10rem 0;
  position: relative;
  z-index: 1;
  &::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.618);
    left: 0;
    top: 0;
    z-index: -1;
  }
  .PageTopCont{
    color: $fon-w;
    font-family: $font-fp;
    h1{
      font-size: 3.2rem;
      text-align:center !important;
      display: inline-block;
    }
    .breadcrumb{
      justify-content: center !important;
    }
    .breadcrumb-item{
      a{
        font-family: $font-fs;
        font-size: 1.6rem;
        font-weight: 500;
        color: $clr-o;
        transition: all linear .3s;
        &:hover{
          color: $fon-w;
        }
      }
    }
  }
}
/* ==================================
  Page Top Componenet End
=================================== */
/* ==================================
  Hero Slider Componenet Start
=================================== */
.heroSlider{
  overflow: hidden;
  .sliderImg{
    width: 100%;
    margin: 0;
    padding: 0;
    min-height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
    &::before{
      position: absolute;
      content: '';
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.55);
      z-index: -1;
    }
    .HeroCont{
      max-width: 45rem;
      color: $fon-w;
      span{
        font-size: 1.6rem;
        margin-bottom: 1rem;
      }
      h1{
        font-size: 4.2rem;
        font-family: $font-fp;
        font-weight: 700;
      }
      p{
        font-family: $font-fs;
        font-size: 1.4rem;
        margin: 1rem 0;
      }
      a.LreanMoreBTN{
        text-decoration: none;
        padding: .5rem 1rem;
        color: $fon-w;
        background-color: $clr-o;
      }
    }
  }
  .NextArr{
    font-size: 3rem !important;
    position: absolute !important;
    color: $clr-o;
    opacity: .6;
    right: 4rem;
    top: 50%;
    transform: translateY(-50%);
    transition: all .3s ease;
    &:hover{
      opacity: 1;
    }
  }
  .PrevArr{
    font-size: 3rem !important;
    position: absolute !important;
    color: $clr-o;
    opacity: .6;
    left: 4rem;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    transition: all .3s ease;
    &:hover{
      opacity: 1;
    }
  }
}
/* ==================================
  Hero Slider Componenet End
=================================== */
/* ==================================
  About Section Componenet Start
=================================== */
.AboutSection{
  margin: 5rem 0;
  .AboutImageSection{
    text-align: center;
    min-height: 50rem;
    position: relative;
    overflow: hidden;
    .AboutImageOne{
      height: 30rem;
      object-fit: cover;
      position: absolute;
      left: 10rem;
      top: 4rem;
      z-index: 3;
    }
    .AboutImageTwo{
      width: 25rem;
      height: 30rem;
      object-fit: cover;
      border: 1rem solid rgb(255, 255, 255);
      -webkit-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      -moz-box-shadow:    0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      box-shadow:         0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      position: absolute;
      left: 25rem;
      bottom: 4rem;
      z-index: 5;
      border-radius: 4px;
    }
  }
  .AboutContent{
    @extend %sec;
    p{
      font-size: 1.6rem;
      margin-bottom: 2rem;
    }
    ol {
      padding: 0;
      li{
      list-style: none;
      }
    }
    @extend %BTC;
  }
}
/* ==================================
  About Section Componenet End
=================================== */
/* ==================================
  Video Section Componenet Start
=================================== */
.VideoSection{
  @extend %BGB;
  padding: 10rem;
  width: 100%;
  position: relative;
  z-index: 1;
  &::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.418);
    left: 0;
    top: 0;
    z-index: -1;
  }
  .VideoBtn{
    a{
      font-size: 6rem;
      color: $fon-w;
    }
  }
  .VideoSectionCont{
    padding: 6rem 4rem;
    background: #ffffff;
    @extend %sec;
  }
}
/* ==================================
  Video Section Componenet End
=================================== */
/* ==================================
  Service Section Componenet Start
=================================== */
.ServiceSection{
  padding: 8rem;
  .ServiceSecCont{
    margin-bottom: 4rem;
    @extend %sec;
    span{
      font-size: 1.6rem;
      font-family: $font-fs;
      font-weight: 400;
      display: block
    }
    @extend %BTC;
  }
  .SingleService{
    padding: 3rem;
    background:#f8f8f8;
    margin-bottom: 4rem;
    transition: all linear .3s;
    span{
      font-size: 4rem;
      color: $fon-b;
      display: block;
      margin-bottom: 2rem;
    }
    h2{
      font-size: 2.4rem;
      margin-bottom: 1.5rem;
    }
    p{
      font-size: 1.6rem;
      font-weight: 500;
      font-family: $font-fs;
      margin-bottom: 1rem;
    }
    a.SerInfo{
      display: inline-block;
      font-size: 2rem;
      color: #ff6c37;
      background: #c2c2c2;
      margin: 1rem 0;
      padding: .5rem 1rem;
      text-align: center;
      line-height: 2rem;
   }
    &:hover{
      background: $clr-o;
      color: #f8f8f8;
      .SerInfo{
        background:#220f089f;
        color: $fon-w;
        transition: all ease-in-out .2s;
        &:hover{
          padding: .5rem 2rem;
        }
      }
    }
  }
}
/* ==================================
  Service Section Componenet End
=================================== */
/* ==================================
  Video Parallax Section Start
=================================== */
.VideoParallax{
  @extend %BGB;
  padding: 8rem 0;
  text-align: center;
  position: relative;
  z-index: 1;
  &::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.418);
    left: 0;
    top: 0;
    z-index: -1;
  }
  span{
    display: inline-block;
    color: $fon-w;
    font-size: 5rem;
    margin-bottom: 2rem;
    transition: all 1s;
    animation: opacity;
    animation-timing-function: linear;
  }
  @keyframes opacity{
    0% { border: 1rem solid #ffffff; }
    100% { border:red; }
  }
  h2{
    font-size: 2.8rem;
    font-family: $font-fp;
    color: $fon-w;
    text-align: center;
    margin-bottom: 3rem;
  }
  @extend %BTC;
  @extend %CTB;
  .BtnBTC,
  .BtnCTB{
    margin: 1rem;
  }
}
/* ==================================
  Video Parallax Section End
=================================== */
/* ==================================
  Testimonial Section Start
=================================== */
.TestimonialSection{
  background-image: url(../images/Testimonial/TestimonialBG.jpg);
  padding: 8rem 0;
  @extend %BGB;
  position: relative;
  z-index: 1;
  &::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.418);
    left: 0;
    top: 0;
    z-index: -1;
  }
  .Testimonial{
    @extend %sec;
    padding: 5rem;
    background: rgba(38, 38, 38, 0.611);
    color: $fon-w;
  }
  .TestimonialSlider{
    .TestiQoute{
      margin: 1rem 0;
      padding: 2rem 0;
      position: relative;
      .QuoteLeft{
        position: absolute;
        left: 0;
        top: 0;
        color: $clr-o;
      }
      p{
        font-size: 1.6rem;
        font-family: $font-fs;
      }
      .QuoteRight{
        position: absolute;
        bottom: 0;
        right: 0;
        color: $clr-o;
      }
    }
  }
  .TestimonialClient{
    .TestClientInfo{
      display: flex !important;
      align-items: center;
      .ClientImg{
        width: 4rem;
        height: 4rem;
        margin-right: 1rem;
      }
      .TestClient{
        h2{
          margin-bottom: 0;
          font-size: 1.6rem;
          font-family: $font-fp;
        }
        p{
          font-size: 1.2rem;
          font-family: $font-fs;
        }
      }
    }
  }
}
/* ==================================
  Testimonial Section End
=================================== */
/* ==================================
  Shedule Section Start
=================================== */
.SheduleSection{
  padding: 8rem 0;
  background-color: rgb(251, 252, 252);
  color: #000;
  @extend %sec;
  .SectionHeading{
    text-align: center;
  }
  .SheduleNav{
    display: flex;
    justify-content: center;
    .SheduleLink{
      color: $fon-w;
      background:  rgb(202, 202, 201) ;
      font-family: $font-fs;
      font-size: 1.6rem;
      font-weight: 500;
      margin: 0 2rem;
      cursor: pointer;
    }
    .SheduleLink.active {
      background:  rgb(60, 59, 58) !important ;
    }
  }
  .TabPaneContainer{
    margin-top: 3rem;
    overflow: hidden;
    border: 1px solid rgb(230, 230, 230);
    .TabHeadiing{
      padding-top: 2rem;
      padding-bottom: 2rem;
      text-align: center;
      font-weight: 600;
      font-family: $font-fp;
      background: rgb(255, 253, 253);
    }
    .SheduleBody{
      padding-top: 2rem;
      padding-bottom: 2rem;
      text-align: center;
      font-family: $font-fs;
      transition: all linear .3s;
      td{
        padding: 2rem 0;
        font-family: $font-fs;
        font-size: 1.6rem;
        font-weight: 600;
      }
      a{
        color: $fon-w;
        background: $clr-o;
        padding:.5rem 1rem;
      }
    }
  }
}
/* ==================================
  Shedule Section End
=================================== */
/* ==================================
  Blog Section Componenet Start
=================================== */
.BlogSection{
  padding: 8rem;
  @extend %sec;
  .SectionHeading{
    text-align: center;
  }
  .SingleBlog{
    .BlogThumb{
      position: relative;
      img{
        width: 100%;
        min-width: 100%;
        object-fit: cover;
      }
      span{
        font-size: 1.4rem;
        padding: .5rem 1rem;
        color: $fon-w;
        background:$clr-o;
        border-radius: .5rem;
        font-weight: 600;
        position: absolute;
        left: 2rem;
        top: 2rem;
      }
    }
    .BlogInfo{
      ol{
        padding: 0;
        li{
          font-size: 1.4rem;
          list-style: none;
          display: inline-block;
          margin-right: 2rem;
          padding: 1rem 0;
          span{
            color: $clr-o;
          }
        }
      }
    }
    h2{
      font-size: 2rem;
      font-weight: 600;
      font-family: $font-fp;
    }
    a.ReadBTN{
      color: $clr-o;
      transition: all linear .2s;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}
/* ==================================
  Blog Section Componenet End
=================================== */
/* ==================================
  Footer Section Componenet End
=================================== */
.FooterWrapper{
  background: rgb(45, 50, 70);
  padding:8rem 0;
  color: $fon-w;
  .FooterSubscribe{
    padding-bottom: 5rem;
    border-bottom: 1px solid #000;
    .SubscribeCont{
      h2{
        font-size: 2.6rem;
        font-family: $font-fp;
        font-weight: 700;
        span{
          color: $clr-o;
        }
      }
      p{
        font-weight: 500;
        font-family: $font-fs;
        font-size: 1.6rem;
      }
    }
    .SubcribeForm{
      .SubscribeEmail{
        padding: 1.5rem;
        width: 100%;
        font-size: 1.6rem;
        outline: 0;
        border: 0;
        background: beige;
        border-radius: 0;
      }
      .SubscribeBTN{
        padding: 1.5rem 4rem;
        font-size: 1.6rem;
        outline: 0;
        border: 0;
        background: rgb(18, 34, 56);
        border-radius: 0;
        font-weight: 700;
      }
    }

  }
  .FooterSection{
    margin-top: 3rem;
    color: $fon-w;
    .FooterHeading{
      font-size: 2.3rem;
      font-family: $font-fp;
      margin-bottom: 3rem;
    }
    .FooterAbout{
      padding-right: 2rem;
      p{
        font-size: 1.6rem;
        margin-bottom: 2rem;
      }
      .FooterStatus{
        text-align: start;
        .ClockIcon{
          display: inline-block;
          font-size: 3rem;
          color: $fon-w;
          background: $clr-o;
          width: 6rem;
          height: 6rem;
          text-align: center;
        }
        h3{
          font-size: 1.6rem;
          display: inline-block;
        }
        p{
          font-size: 1.4rem;
          margin-bottom: 0;
        }
        span{
          font-size: 1.3rem;
          color: $clr-o;
        }
      }
    }
  }
  .FooterLinks,
  .FooterHelp{
    ul{
      li{
        a{
          font-size: 1.4rem !important;
          color: #fcfcfc !important;
          font-family: "Karla", sans-serif;
          text-decoration: none;
          font-weight: 400;
          padding: 0;
        }
      }
    }
  }
  .FooterRecentPost{
    .FooterShortPost{
      font-family: $font-fs;
      color:$fon-w;
      margin: .5rem 0;
      img{
        width: 6rem;
        max-width: 100%;
        object-fit: cover;
      }
      .FooterShortPostDate{
        font-size: 1rem;
        font-weight: 300;
      }
      h3{
        font-size: 1.6rem;
        color: $fon-w;
        text-align: start;
      }
    }
  }
}
/* ==================================
  Footer Section Componenet End
=================================== */
/* ==================================
  CopyRight Section Componenet End
=================================== */
.CopyRight{
  background: rgb(21, 23, 48);
  color: $fon-w;
  font-family: $font-fs;
  padding: 2rem 0;
  .FooterLogo{
    width: 100%;
    max-width: 5rem;
  }
  h1{
    font-family: $font-fs;
    font-size: 1.6rem;
    font-weight: 600;
    margin-right: 1rem;
    span{
      color: $clr-o;
      font-size: 3rem;
    }
  }
  p{
    font-weight: 600;
    font-size: 1.6rem;

    a{
      font-weight: 700;
    }
  }
  .CopySocials{
    a{
      width: 3.5rem;
      height: 3.5rem;
      background: #4f4e4e;
      color: #fcfcfc;
      font-size: 1.6rem;
      margin: 0 .5rem;
      text-align: center;
      line-height: 3.5rem;
  
    }
  }
}
/* ==================================
  CopyRight Section Componenet End
=================================== */
/* ==================================
  Blog Page Start
=================================== */
.PagesSection{
  margin: 8rem 0;
  .BlogLeft{
    overflow: hidden;
  }
  .BlogWidget{
    overflow: hidden;
  }
}
.PagesSection{
  .SingleBlog{
    height: 37rem;
    margin-bottom: 3rem;
    max-width: 100%;
    position: relative;
    .BlogThumb{
      img{
        width: 100%;
        max-width: 100%;
      }
      span{
        font-size: 1.4rem;
        padding: .5rem 1rem;
        color: $fon-w;
        background:$clr-o;
        border-radius: .5rem;
        font-weight: 600;
        position: absolute;
        left: 2rem;
        top: 2rem;
      }
    }
    .BlogInfo{
      position: absolute;
      bottom: 0;
      background:rgb(235, 247, 252);
      padding: 2rem;
      margin: 2rem;
      box-shadow: 0px 0px 8px 3px rgba(0,0,0,0.21);
      -webkit-box-shadow: 0px 0px 8px 3px rgba(0,0,0,0.21); 
      ol{
        padding: 0;
        li{
          font-size: 1.4rem;
          list-style: none;
          display: inline-block;
          margin-right: 2rem;
          padding: 1rem 0;
          span{
            color: $clr-o;
          }
        }
      }
    }
    h2{
      font-size: 2rem;
      font-weight: 600;
      font-family: $font-fp;
    }
    a.ReadBTN{
      color: $clr-o;
    }
  }
}
/* ==================================
  Blog Page End
=================================== */
/* ==================================
  Blog Widget Section Start
=================================== */
.SearchWidget,
.CategoriesWidget,
.RecentWidget,
.InstragamWidget,
.RecentTagWidget{
  padding: 2rem;
  margin:.3rem 1rem;
  box-shadow: 0px 0px 8px 3px rgba(0,0,0,0.21);
  -webkit-box-shadow: 0px 0px 8px 3px rgba(0,0,0,0.21); 
  .WidgetHeading{
    font-size: 2rem;
    font-family: $font-fp;
    font-weight: 700;
    margin-bottom: 1rem !important;
  }
}
.SearchWidget{
  .CateForm{
    display: flex;
    .form-control,.form-select {
    font-size: 1.4rem !important;
    border:1px solid red;
    border-radius: 0;
    background: none;
    outline: none;
    }
    .SearchIcon{
      font-size: 2rem !important;
      border: 1px solid red;
      background: #e72828 !important;
      line-height: 1rem;
      color: #fff;
      padding: 0 1rem;
      border-radius: 0;
    }
  }
}
.CategoriesWidget{
  margin-top: 3rem;
  ul{
    padding-left: 0;
    li{
      list-style: none;
      a{
        display: flex;
        justify-content: space-between;
        padding: 1rem 0rem;
        border-bottom: 1px solid rgb(158, 153, 153);
        cursor: pointer;
        transition:all linear .3s;
        &:hover{
          border-color: #ff6c37;
        }
      }
    }
  }
}
.RecentWidget{
  margin-top: 3rem;
  font-family: $font-fs;
  color:$fon-b;
  .SingleRecent{
    display: flex;
    align-items: start;
    justify-content: center;
    margin-top: 2rem;
    border-bottom: 1px dashed rgb(158, 153, 153);
    img{
      width: 6rem;
      max-width: 100%;
      object-fit: cover;
    }
    .FooterShortPostDate{
      font-size: 1.2rem;
      font-weight: 300;
    }
    h3{
      font-size: 1.6rem;
      color: $fon-b;
      text-align: start;
    }
  }
}
.InstragamWidget{
  margin-top: 3rem;
  .InsPostImages{
    display: flex;
    flex-wrap: wrap;
    grid-gap: .5rem;
    img{
      width: 6rem;
      min-width: 100%;
      height: 6rem;
      border-radius: 4px;
      object-fit: cover;
    }
  }
}
.RecentTagWidget{
  margin-top: 3rem;
  .RecentTagMenu{
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    li{
      list-style: none;
      a{
