=================================== */
/* ==================================
  Blog Details Section Start
=================================== */
.BlogDetails{
  h1{
    font-size: 3rem;
  }
  p{
    font-size: 1.8rem;
    font-family: $font-fs;
    color: $fon-b;
    span{
      margin: 2rem 0;
      display: block;
      font-size: 1.8rem;
      font-family: $font-fs;
      color: $fon-b;
    }
  }
  .AdminDetails{
    display: flex;
    align-items: center;
    margin: 3rem 0;
    .CategorieName{
      padding: 1rem 2rem;
      color: $fon-w;
      background:$clr-o;
    }
    .BlogUserDetails{
      margin-left: 3rem;
      display: flex;
      align-items: center;
      line-height: normal;
      img{
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        margin-right: 1rem;
      }
      .UserDetails{
        font-family: $font-fs;
        h2{
          font-size: 1.6rem;
          margin-bottom: 0;
        }
        span{
          font-size: 1.2rem;
        }
      }
    }
  }
  .BlogBigImages{
    position: relative;
    .BlogMainImage{
      width: 100%;
      max-width: 100%;
      height: 35rem;
      object-fit: cover;
      margin-bottom: 3rem;
    }
    .BlogQute{
      position: absolute;
      width: 90%;
      padding: 3rem 2rem;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      background: grey;
      text-align: center;
      -webkit-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      -moz-box-shadow:    0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      box-shadow:         0px 0px 8px 0px rgba(50, 50, 50, 0.42);
      span{
        font-size: 1.3rem;
        font-family: $font-fp;
        display: inline-block;
        color: $fon-b;
        margin-bottom: 1rem;
      }
      p{
        color: $fon-w;
        font-size: 2rem;
        font-family: $font-fs;
      }
    }
  }
  .BlogMoreImage{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    margin: 2rem 0;
    img{
      width: 15rem;
      max-width: 100%;
    }
  }
  .DetailsButtom{
    .DetailsTop{
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #000;
      .DetailsTAg{
        display: flex;
        justify-content: space-between;
        margin-top: 2rem;
        padding: 3rem 0;
        h3{
          font-size: 2rem;
          font-weight: 600;
          color: $fon-b;
          display: inline-block;
        }
        ol{
          display: flex;
          li{
            list-style: none;
            a{
              padding: .5rem 1rem;
              margin: 0 .5rem;
              background:rgb(218, 218, 218);
              color: $fon-b;
              font-size: 1.2rem;
              font-weight: 500;
              letter-spacing: 1px;
            }
          }
        }
      }
      .DetailsSocials{
        a{
          font-size: 2rem;
          color: $fon-b;
          margin: 0 1rem;
          transition: all linear .3s;
          &:hover{
            color: $clr-o;
          }
        }
      }
    }
    .DetailsPagination{
      margin:2rem 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      a{
        font-size: 1.4rem;
        color: #000;
        margin: 0 1rem;
        transition: all linear .3s;
        display: flex;
        align-items: center;
        background: rgb(221, 221, 221);
        padding: .5rem 1rem;
        font-weight: 500;
        .LeftArr{
          margin-right: 1rem;
        }
        .RightArr{
          margin-left: 1rem;
        }
        &:hover{
          color: $clr-o;
        }
      }
      span{
        font-size: 3rem;
      }
    }
  }
}
/* ==================================
  Blog Details Section End
=================================== */
/* ==================================
  Contact Section Start
=================================== */
.contactSection{
  padding: 8rem 0;
  .contactDetails{
      color: $fon-b;
      padding: 0 40px;
      padding-bottom: 30px;
      h3{
          font-size: 14px;
          font-weight: 500;
          font-family: $font-fs;
      }
      h2{
          font-size: 36px;
          font-family: $font-fs;
          font-weight: 600;
          margin: 15px 0;
          span{
              display: block;
          }
      }
      p{
          font-size: 16px;
          font-weight: 300;
      }
      .contactLinks{
          margin-top: 30px;
          a{
            min-width: 40px;
            height: 40px;
            border-radius: 50%;
            text-align: center;
            color: #fcfcfc;
            background: #e72828;
            font-size: 1.6rem;
            transition: all linear .3s;
            margin: 0 .5rem;
            line-height: 3.2rem;
            text-align: center;
            vertical-align: middle;
              transition: all linear .3s;
              &:hover{
                  background: $fon-b;
              }
          }
      }
  }
  .contactForm{
      padding: 0 40px;
      .form-control{
          display: block;
          width: 100%;
          padding: 1rem;
          font-size: 14px;
          font-weight: 400;
          line-height: 1rem;
          color: #212529;
          background: none;
          background-clip: padding-box;
          outline: none;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          -webkit-transition: border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
          transition: border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
          transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
          transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
      }
      .emailText {
          margin-top: .25rem;
          font-size: 1.4rem;
          color: #c1c0c0;
      }
      textarea#validationTextarea {
          height: 120px;
      }
      .formSendBtn{
        padding: .5rem 1rem !important;
        width: fit-content;
        margin-top: 2rem;
        background: $fon-b;
        font-size: 1.4rem;
        font-weight: 600;
        color: $fon-w !important;
        border: none;
        border-radius: 0 !important;
        transition: all linear .3s;
      &:hover{
          background: $clr-o;
      }
      }
  }
}
/* ==================================
  Contact Section End
=================================== */

